<template>
    <div>
      <top/>
        <!--<topMember :index="2"></topMember>-->
        <div class="box mar fffbg" style="min-height:500px;">
            <div class="mainH">
                <div class="mainHICO"></div>
                <span class="pull-left">绑定店铺</span>
            </div>
            <div class=" over" style="margin: 20px 1% 0 1%">
               <el-form inline="true">
                 <el-form-item>
                   <el-select v-model="type" >
                     <el-option  key="1" label="淘宝/天猫" value="1"></el-option>
                     <el-option  key="2" label="拼多多" value="2"></el-option>
                   </el-select>
                 </el-form-item>
                 <el-form-item>
                   <el-button @click="addShop" type="primary">添加平台</el-button>
                 </el-form-item>
                 <!--<el-form-item>-->
                   <!--<span>淘宝/天猫输入-->
                     <!--<span style="color: red">店铺主账号</span>-->
                     <!--,拼多多输入<span style="color: red">店铺ID</span></span>-->
                 <!--</el-form-item>-->
               </el-form>
            </div>
          <el-table
            :data="tableData"
            border
            :header-cell-style="{background:'#f7f7f7'}"
            style="width: 94%;margin: 10px 1%"
          >
            <el-table-column prop="work" label="平台" align="center" >
              <template slot-scope="scope">
                <span >{{scope.row.type === 2?'拼多多':'淘宝/天猫'}}</span>
              </template>
            </el-table-column>
            <el-table-column prop="shopName" label="店铺名称" align="center" />
            <el-table-column prop="createTime" label="提交时间" align="center" >
              <template slot-scope="scope">
                <span >{{moment(scope.row.createTime).format("YYYY-MM-DD HH:mm")}}</span>
              </template>
            </el-table-column>

            <el-table-column prop="endTime" label="到期截止时间" align="center" >
              <template slot-scope="scope">
                <span >永久</span>
              </template>
            </el-table-column>
          </el-table>
          <div class="tip">
            <span>1.绑定店铺，能自动上传到对应平台订单的发货信息；</span><br/>
            <span>2.绑定核验淘宝/天猫店铺步骤：①服务市场订购：点击<a :href="urlList.taobao" target="_blank" style="color:red">淘宝前往订购</a>②选择订购有效期 ③点击添加绑定店铺，跳转授权验证店铺。</span><br/>
            <span>3.绑定核验拼多多店铺步骤：①服务市场订购：点击<a :href="urlList.pdd" target="_blank" style="color:red">拼多多前往订购</a>②选择订购有效期 ③点击添加绑定店铺，跳转授权验证店铺。</span><br/>
            <span>4.自动发货：当任务状态为‘已完成’时，系统会自动更新淘宝后台对应订单的物流信息。</span>
          </div>
        </div>
        <foot></foot>


    </div>
</template>

<script>
import foot from "./public/foot.vue";
import top from "./public/top.vue";
import moment from "moment";
export default {
    components: {
        foot,top,
    },
    data() {
        return {
          type:'1',
          tableData:[],
          urlList:[],
        };
    },
    mounted() {
        this.getUrlList();
        this.getList();
    },
    methods: {
      moment,
      getList() {
        this.axios
          .get(
            // process.env.API_HOST+"goods?page="+(that.page-1)+"&size="+that.size,
            "/giftmall/goods",
            {
              headers: {
                "Content-Type": "application/x-www-form-urlencoded"
              }
            }
          )
          .then(response => {
            if (response.data.code == 200) {
              this.tableData = response.data.data;
            }
          });
      },
        getUrlList() {
            this.axios
                .get(
                    // process.env.API_HOST+"goods?page="+(that.page-1)+"&size="+that.size,
                    "/giftmall/agiso/url",
                    {
                        headers: {
                            "Content-Type": "application/x-www-form-urlencoded"
                        }
                    }
                )
                .then(response => {
                    if (response.data.code == 200) {
                      this.urlList = response.data.data;
                    }
                });
        },
      addShop(){
        let url;
        if(this.type === '1'){
           url = this.urlList.taobao;
        }else{
           url = this.urlList.pdd;
        }
        window.open(url);
      },
    }
};
</script>
<style lang="less">
	.el-table .warning-row {
		background: #fdebec;
	}
</style>
<style scoped lang="less">
  .tip{
    margin: 0px 1%;
    font-size: 12px;
    line-height: 20px;
    color:gray;
  }

</style>



